/* Navbar */
.navbar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 8px 120px 1.6px 120px;
    position: absolute;
    z-index: 1;
    width: 100%;
}

.navbar-sticky {
    background: #285E4D;
    position: fixed;
    top: 0;
    left: 0;
    box-shadow: 1px 1px 1px #222;
    animation: moveDown 0.5s ease-in-out;
}

.navbar--logo-holder {
    display: flex;
    align-items: center;
    justify-content: center;
}

.navbar--logo {
    width: 128px;
    height: 32px;
    margin-right: 8px;
    animation: rotate 0.7s ease-in-out 0.5s;
}

.navbar--link {
    display: flex;
    list-style: none;
    align-items: center;
    /* width: 75%;
    justify-content: flex-end; */
}

.navbar--link-item {
    margin: 6px 16px 0 0;
    padding: 0 4px;
    color: #C2DC55;
    cursor: pointer;
    font-size: 18px;
    font-weight: 500;
}

@keyframes moveDown {
    from {
        transform: translateY(-5rem);
    }

    to {
        transform: translateY(0rem);
    }
}

@keyframes rotate {
    0% {
        transform: rotateY(360deg);
    }

    100% {
        transform: rotateY(0rem);
    }
}


.mxContainerData {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 8px 120px 1.6px 120px;
    height: 100vh;
    width: 100vw;
}

.mxButton {
    border: 1px solid #C2DC55;
    border-radius: 12px;
    font-size: 13.24px;
    font-weight: 500;
    background-color: #C2DC55;
    color: #285E4D;
    padding: 16px 32px 16px 32px;
}

.mxTopHead {
    font-size: 64px;
    font-weight: 700;
    color: #fff;
    line-height: 80px;
}

.mxTopSubHead {
    font-size: 28px;
    font-weight: 400;
    color: #fff;
    line-height: 42px;
}

.mxSubHead {
    font-size: 18px;
    font-weight: 400;
    color: #fff;
    line-height: 27px;
    margin-top: 40px;
}

.mxEmptyBox {
    height: 500px;
    background-color: #4F7E4F;
    border-radius: 18.25px;
}

.mxHead {
    font-size: 48px;
    font-weight: 700;
    color: #333;
    line-height: 67px;
}

.mxBoxes {
    background-color: #F5F5F5;
    border: 1px solid #e5e5e5;
    border-radius: 24px;
    padding: 10px 50px 10px 50px;
    display: flex;
    flex-direction: column;
    max-width: 400px;
    width: 400px;
    min-height: 50%;
}

.mxBoxesImg {
    height: 100px;
    width: 100px;
    float: left;
}

.mxOptionHead {
    float: left;
    font-size: 28px;
    font-weight: 500;
    line-height: 36.46px;
    color: #333;
}

.mxOptionContent {
    float: left;
    font-size: 24px;
    font-weight: 400;
    line-height: 36px;
    color: #6c6c6c;
}

.mxSubSmallHead {
    font-size: 12px;
    color: #fff;
    line-height: 15.62px;
}

.mxSubContainer {
    align-content: 'center';
    display: flex;
    margin-top: 5.5%;
}

.hide-scrollbar {
    overflow-y: auto;
    /* white-space: nowrap; */
    -ms-overflow-style: none;
    /* IE and Edge */
    scrollbar-width: none;
    /* Firefox */
}


